.btnHome {
    width: 5%;
    cursor: pointer;
    margin: 1rem;
  }

  .subMenu {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }
  
  .client {
    grid-area: 1 / 1 / 2 / 2;
  }
  
  .waiter {
    grid-area: 1 / 2 / 2 / 3;
  }
  
  #breakfastButton {
    grid-area: 2 / 1 / 3 / 2;
  }
  
  #principalMenuButton {
    grid-area: 2 / 2 / 3 / 3;
  }
  
  #drinkButton {
    grid-area: 2 / 3 / 3 / 4;
  }
  
